import "./css/app.scss";
import f01 from "../assets/img/f01.jpg";
import f01x from "../assets/img/f01x.jpg";

import React, { Component, Fragment } from "react";

import Animation from "./Animation";
import Header from "./Header";

class App extends Component {
	state = {
		isVisible: true,
		isNone: false
	};

	componentDidMount() {
		setTimeout(() => {
			this.setState({
				isVisible: false
			});
		}, 8000);
		setTimeout(() => {
			this.setState({
				isNone: true
			});
		}, 8500);
	}
	render() {
		return (
			<Fragment>
				<Animation
					visible={this.state.isVisible}
					none={this.state.isNone}
				/>
				<Header />
			</Fragment>
		);
	}
}

export default App;
